rue\n    };\n  },\n  computed: {\n    ...mapGetters({\n      // refundNoticeStatus: \"refundNoticeStatus\"\n    })\n  },\n  mounted() {\n    this.changeHeader();\n    // 退票信息\n    this.getReturnInfo();\n    this.Omega(\"train_ticket_refund_sw\", \"申请退票页面展现\");\n  },\n  methods: {\n    ...mapActions([\n      \"changeHeader\"\n      // \"updateRefundNoticeStatus\"\n    ]),\n    // 退票信息\n    getReturnInfo() {\n      this.ticketInfo = this.$route.query;\n    },\n    applyRefund() {\n      if (this.queryStatus) {\n        this.queryStatus = false;\n        this.$createDialog({\n          type: \"confirm\",\n          content: \"确定提交退票申请吗？\",\n          confirmBtn: {\n            text: \"确定\",\n            active: true\n          },\n          cancelBtn: {\n            text: \"取消\",\n            active: false\n          },\n          onConfirm: () => {\n            // 请求取消退票的接口\n            this.fetchReturnTicket();\n          },\n          onCancel: () => {\n            this.queryStatus = true;\n          }\n        }).show();\n        this.Omega(\n          \"train_ticket_refund_check_ck\",\n          \"申请退票页面申请退票按钮点击\"\n        );\n      }\n    },\n    // 退票请求\n    async fetchReturnTicket() {\n      const toast = this.$createToast({\n        time: 0,\n        txt: \"正在申请退票...\"\n      });\n      toast.show();\n      const params = {\n        order_id: this.ticketInfo.order_id,\n        ticket_ids: this.ticketInfo.ticket_id\n      };\n      const res = await api.returnTicket(params);\n      toast.hide();\n      if (res && res.errno === 0) {\n        // 退票成功 —— 跳转订单详情页\n        this.$router.push({\n          path: \"/orderDetail\",\n          query: {\n            order_id: this.ticketInfo.order_id\n          }\n        });\n      } else {\n        // 退票失败\n        this.$createDialog({\n          type: \"alert\",\n          content: res.errmsg || \"申请退票失败，请稍后重试\",\n          confirmBtn: {\n            text: \"我知道了\",\n            active: true\n          },\n          onC